.homeWrap {
    
    .article2 {
        .row {
            padding: 80px 0;
            h2 { font-size:24px; margin-left:20px; }
            .local { display:flex; flex-wrap:wrap;
                padding:40px 0 0 0; 
                li { width:33.33%; padding:10px ; text-align:center;
                    a { 
                      img {width:95%; margin-bottom:10px; border-radius:15px; }
                    }
                }
            }
            // .lifeStyle {
            //     h2 { font-size:20px }
            //     ul { display:flex; 
            //         li { flex:1; padding:20px }
            //     }
            // }
        }
    }

    .article3{
        background:rgb(221, 221, 221); color: #fff; padding: 80px 0 50px 0;
        h2 { 
            font-size:24px; color: #000;
            max-width: 1200px; margin:0px auto; padding-left:20px;
        }
        .row { display:flex; padding:35px 10px 0 10px;
            div { flex:0.75;
                img { width:100%; padding:20px 10px 0px; 
                    border-radius:35px 35px 0px 0px; }
                p { background:#da272c; margin:0px 10px; 
                    padding:15px 0 15px 25px; border-radius:0px 0px 20px 20px;
                    font-weight: bold;
                }
            &.right { flex:1;
                display:flex; flex-direction:column; justify-content:space-between;
               .rightRow1 { 
                   display:flex; flex-direction:row; 
                   div:nth-child(1) img{height:80%;} 
                   div:nth-child(2) img{height:80%;} 
                }
                .rightRow2 {
                    img{height:84.5%;}
                }
            }
            }
        }

    }
} 